import React from 'react';
import './About.css';

const About = ({ summary, skills }) => {
    return (
        <section className="my-8 p-8 rounded-2xl bg-gradient-to-br from-darkBlue/90 to-darkBg/90 shadow-2xl border-2 border-neonCyan/40 hover:border-neonPurple/60 animate-slide-in-left transition-all duration-300 relative overflow-hidden group">
            <div className="absolute -top-32 -right-32 w-64 h-64 bg-neonCyan/10 rounded-full blur-3xl group-hover:bg-neonPurple/15 transition-all duration-500"></div>
            <h2 className="text-2xl md:text-3xl font-bold bg-gradient-to-r from-neonCyan via-neonPurple to-neonGreen bg-clip-text text-transparent mb-6 flex items-center gap-3 relative z-10">
                <span className="text-4xl">üë®‚Äçüíª</span>
                About Me
            </h2>
            {(() => {
                const parts = summary.split('\n\n');
                const coreStrengths = parts[parts.length - 1]?.startsWith('Core Strengths:')
                    ? parts.pop()
                    : null;
                const strengthsItems = coreStrengths
                    ? coreStrengths.replace('Core Strengths:', '').split('‚Ä¢').map((item) => item.trim()).filter(Boolean)
                    : [];
                const extraStrengths = ['SQL', 'GCP', 'Python', 'Power BI'];
                const mergedStrengths = [...strengthsItems, ...extraStrengths].filter(Boolean);

                return (
                    <>
                        {parts.map((paragraph, idx) => (
                            <p key={idx} className="text-sm md:text-base text-gray-300 mb-4 leading-relaxed relative z-10">
                                {paragraph}
                            </p>
                        ))}
                        {mergedStrengths.length > 0 && (
                            <div className="mt-2 relative z-10">
                                <h4 className="text-lg font-semibold text-neonCyan mb-2">Core Strengths</h4>
                                <p className="text-sm text-gray-200 leading-relaxed">
                                    {mergedStrengths.join('  .  ')}
                                </p>
                            </div>
                        )}
                    </>
                );
            })()}
        </section>
    );
};

export default About;